import React, { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '../../components/ui/card';
import { Button } from '../../components/ui/button';
import { Input } from '../../components/ui/input';
import { Badge } from '../../components/ui/badge';
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '../../components/ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '../../components/ui/select';
import { Label } from '../../components/ui/label';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { toast } from 'sonner';
import {
  Users,
  Plus,
  Search,
  Filter,
  UserPlus,
  Mail,
  Phone,
  MapPin,
  Shield,
  Calendar,
  Edit,
  Trash2,
  AlertCircle,
  Loader2
} from 'lucide-react';
import {
  getAllClinicalUsers,
  getClinicalUserStats,
  createClinicalUser,
  assignClinicalRole,
  getAvailableUsers
} from '../../api/services/clinicalUser/clinicalUserService';
import { getAllClinicalRoles } from '../../api/services/clinicalRole/clinicalRoleService';
import { getAllClinicalSites } from '../../api/services/clinicalSite/clinicalSiteService';

const ClinicalUsers = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [selectedRole, setSelectedRole] = useState('');
  const [selectedSite, setSelectedSite] = useState('');
  const queryClient = useQueryClient();

  // Form state for creating user
  const [createFormData, setCreateFormData] = useState({
    name: '',
    email: '',
    phone: '',
    clinical_role: '',
    clinical_site: ''
  });
  const [showFilters, setShowFilters] = useState(false);
  const [selectedUser, setSelectedUser] = useState(null);

  // Fetch clinical users
  const { data: usersData, isLoading: usersLoading, error: usersError } = useQuery({
    queryKey: ['clinicalUsers'],
    queryFn: getAllClinicalUsers,
    retry: 1,
  });

  // Fetch user statistics
  const { data: statsData, isLoading: statsLoading } = useQuery({
    queryKey: ['clinicalUserStats'],
    queryFn: getClinicalUserStats,
    retry: 1,
  });

  // Fetch clinical roles for assignment
  const { data: rolesData, isLoading: rolesLoading, error: rolesError } = useQuery({
    queryKey: ['clinicalRoles'],
    queryFn: getAllClinicalRoles,
    retry: 1,
  });

  // Fetch clinical sites for assignment
  const { data: sitesData } = useQuery({
    queryKey: ['clinicalSites'],
    queryFn: getAllClinicalSites,
    retry: 1,
  });

  const clinicalUsers = usersData?.data || [];
  const userStats = statsData?.data || {
    total_users: 0,
    active_users: 0,
    inactive_users: 0,
    users_with_roles: 0
  };
  const availableRoles = rolesData?.data || [];
  const clinicalRoles = rolesData?.data || [];
  const clinicalSites = sitesData?.data || [];

  // Create user mutation
  const createUserMutation = useMutation({
    mutationFn: createClinicalUser,
    onSuccess: () => {
      toast.success('Clinical user created successfully');
      queryClient.invalidateQueries({ queryKey: ['clinicalUsers'] });
      queryClient.invalidateQueries({ queryKey: ['clinicalUserStats'] });
      setIsCreateDialogOpen(false);
      setCreateFormData({
        name: '',
        email: '',
        phone: '',
        clinical_role: '',
        clinical_site: ''
      });
    },
    onError: (error) => {
      toast.error('Failed to create clinical user');
      console.error('Create user error:', error);
    }
  });

  // Role assignment mutation
  const assignRoleMutation = useMutation({
    mutationFn: ({ userId, roleData }) => assignClinicalRole(userId, roleData),
    onSuccess: () => {
      toast.success('Role assigned successfully');
      queryClient.invalidateQueries({ queryKey: ['clinicalUsers'] });
      queryClient.invalidateQueries({ queryKey: ['clinicalUserStats'] });
      setIsCreateDialogOpen(false);
      setSelectedRole('');
    },
    onError: (error) => {
      toast.error(error.response?.data?.message || 'Failed to assign role');
    },
  });

  // Form handlers
  const handleCreateFormChange = (field, value) => {
    setCreateFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleCreateUser = () => {
    if (!createFormData.name || !createFormData.email) {
      toast.error('Please fill in all required fields');
      return;
    }
    createUserMutation.mutate(createFormData);
  };

  // Additional handler functions for button functionality
  const handleEditUser = (user) => {
    setSelectedUser(user);
    toast.info(`Opening edit form for ${user.first_name} ${user.last_name}`);
    // TODO: Implement edit user functionality
  };

  const handleAssignStudies = (user) => {
    toast.info(`Opening study assignment for ${user.first_name} ${user.last_name}`);
    // TODO: Implement study assignment functionality
  };

  const handleViewProfile = (user) => {
    toast.info(`Viewing profile for ${user.first_name} ${user.last_name}`);
    // TODO: Navigate to user profile page or open profile modal
  };

  const toggleFilters = () => {
    setShowFilters(!showFilters);
  };

  const filteredUsers = clinicalUsers.filter(user => {
    const fullName = `${user.first_name || ''} ${user.last_name || ''}`.trim();
    const email = user.email || '';
    const role = user.clinical_role_display || '';
    const site = user.site_name || '';
    const phone = user.phone_number || user.phone || '';

    return fullName.toLowerCase().includes(searchTerm.toLowerCase()) ||
           email.toLowerCase().includes(searchTerm.toLowerCase()) ||
           role.toLowerCase().includes(searchTerm.toLowerCase()) ||
           site.toLowerCase().includes(searchTerm.toLowerCase()) ||
           phone.toLowerCase().includes(searchTerm.toLowerCase());
  });

  const getStatusBadge = (status) => {
    return status === 'Active' ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800';
  };

  const getRoleBadge = (role) => {
    const roleColors = {
      'Principal Investigator': 'bg-purple-100 text-purple-800',
      'Study Coordinator': 'bg-blue-100 text-blue-800',
      'Site Manager': 'bg-green-100 text-green-800',
      'Recruiter': 'bg-orange-100 text-orange-800',
      'Data Entry Specialist': 'bg-gray-100 text-gray-800',
      'Biostatistician': 'bg-indigo-100 text-indigo-800',
      'Regulatory Affairs': 'bg-red-100 text-red-800',
      'Data Manager': 'bg-cyan-100 text-cyan-800',
      'Clinical Research Associate (CRA)': 'bg-yellow-100 text-yellow-800',
      'Clinical Research Coordinator (CRC)': 'bg-pink-100 text-pink-800',
      'Sub Investigator': 'bg-violet-100 text-violet-800'
    };
    return roleColors[role] || 'bg-gray-100 text-gray-800';
  };

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Clinical Users</h1>
          <p className="text-gray-600 mt-1">Manage clinical research staff and role assignments</p>
        </div>
        
        <Dialog open={isCreateDialogOpen} onOpenChange={setIsCreateDialogOpen}>
          <DialogTrigger asChild>
            <Button>
              <Plus className="h-4 w-4 mr-2" />
              Add Clinical User
            </Button>
          </DialogTrigger>
          <DialogContent className="max-w-md" aria-describedby="user-form-description">
            <DialogHeader>
              <DialogTitle>Add New Clinical User</DialogTitle>
              <DialogDescription id="user-form-description">
                Fill out the form below to add a new clinical staff member. All required fields must be completed.
              </DialogDescription>
            </DialogHeader>
            <div className="space-y-4">
              <div>
                <Label htmlFor="name">Full Name *</Label>
                <Input
                  id="name"
                  placeholder="Dr. John Smith"
                  value={createFormData.name}
                  onChange={(e) => handleCreateFormChange('name', e.target.value)}
                />
              </div>
              <div>
                <Label htmlFor="email">Email *</Label>
                <Input
                  id="email"
                  type="email"
                  placeholder="john.smith@hospital.org"
                  value={createFormData.email}
                  onChange={(e) => handleCreateFormChange('email', e.target.value)}
                />
              </div>
              <div>
                <Label htmlFor="phone">Phone</Label>
                <Input
                  id="phone"
                  placeholder="+1-555-0123"
                  value={createFormData.phone}
                  onChange={(e) => handleCreateFormChange('phone', e.target.value)}
                />
              </div>
              <div>
                <Label htmlFor="role">Clinical Role</Label>
                <Select value={createFormData.clinical_role} onValueChange={(value) => handleCreateFormChange('clinical_role', value)}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select clinical role" />
                  </SelectTrigger>
                  <SelectContent>
                    {rolesLoading ? (
                      <SelectItem value="loading" disabled>Loading roles...</SelectItem>
                    ) : clinicalRoles.length === 0 ? (
                      <SelectItem value="no-roles" disabled>No roles available</SelectItem>
                    ) : (
                      clinicalRoles.map((role) => (
                        <SelectItem key={role.id} value={role.name}>
                          {role.display_name}
                        </SelectItem>
                      ))
                    )}
                  </SelectContent>
                </Select>
              </div>
              <div>
                <Label htmlFor="site">Clinical Site</Label>
                <Select value={createFormData.clinical_site} onValueChange={(value) => handleCreateFormChange('clinical_site', value)}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select clinical site" />
                  </SelectTrigger>
                  <SelectContent>
                    {clinicalSites.map((site) => (
                      <SelectItem key={site.id} value={site.id.toString()}>
                        {site.site_name || site.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div className="flex justify-end space-x-2 pt-4">
                <Button variant="outline" onClick={() => setIsCreateDialogOpen(false)}>
                  Cancel
                </Button>
                <Button
                  onClick={handleCreateUser}
                  disabled={createUserMutation.isPending}
                >
                  {createUserMutation.isPending ? 'Creating...' : 'Create User'}
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>
      </div>

      {/* Statistics Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Users</CardTitle>
            <Users className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            {statsLoading ? (
              <div className="text-2xl font-bold">--</div>
            ) : (
              <div className="text-2xl font-bold">{userStats.total_users}</div>
            )}
            <p className="text-xs text-muted-foreground">Active clinical staff</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Active Users</CardTitle>
            <Shield className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            {statsLoading ? (
              <div className="text-2xl font-bold">--</div>
            ) : (
              <div className="text-2xl font-bold">{userStats.active_users}</div>
            )}
            <p className="text-xs text-muted-foreground">Currently active</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Users with Roles</CardTitle>
            <Users className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            {statsLoading ? (
              <div className="text-2xl font-bold">--</div>
            ) : (
              <div className="text-2xl font-bold">{userStats.users_with_roles}</div>
            )}
            <p className="text-xs text-muted-foreground">Assigned roles</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Inactive Users</CardTitle>
            <MapPin className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            {statsLoading ? (
              <div className="text-2xl font-bold">--</div>
            ) : (
              <div className="text-2xl font-bold">{userStats.inactive_users}</div>
            )}
            <p className="text-xs text-muted-foreground">Need attention</p>
          </CardContent>
        </Card>
      </div>

      {/* Search and Filter */}
      <Card>
        <CardHeader>
          <div className="flex justify-between items-center">
            <CardTitle>Clinical Staff Directory</CardTitle>
            <div className="flex space-x-2">
              <div className="relative">
                <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
                <Input
                  placeholder="Search users..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="pl-8 w-64"
                />
              </div>
              <Button variant="outline" size="sm" onClick={toggleFilters}>
                <Filter className="h-4 w-4 mr-2" />
                Filter
              </Button>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          {usersLoading ? (
            <div className="flex items-center justify-center py-12">
              <Loader2 className="h-8 w-8 animate-spin text-gray-400" />
              <span className="ml-2 text-gray-600">Loading clinical users...</span>
            </div>
          ) : usersError ? (
            <div className="text-center py-12">
              <AlertCircle className="h-12 w-12 text-red-300 mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-gray-900 mb-2">Error Loading Users</h3>
              <p className="text-gray-600 mb-4">Failed to load clinical users. Please try again.</p>
            </div>
          ) : filteredUsers.length === 0 ? (
            <div className="text-center py-12">
              <Users className="h-12 w-12 text-gray-300 mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-gray-900 mb-2">No Users Found</h3>
              <p className="text-gray-600 mb-4">
                {searchTerm ? 'No users match your search criteria.' : 'No clinical users have been added yet.'}
              </p>
              <Button onClick={() => setIsCreateDialogOpen(true)}>
                <Plus className="h-4 w-4 mr-2" />
                Add First User
              </Button>
            </div>
          ) : (
            <div className="space-y-4">
              {filteredUsers.map((user) => (
              <div key={user.id} className="border border-gray-200 rounded-lg p-6 hover:shadow-md transition-shadow">
                <div className="flex justify-between items-start mb-4">
                  <div>
                    <h3 className="text-lg font-semibold text-gray-900">
                      {`${user.first_name || ''} ${user.last_name || ''}`.trim()}
                    </h3>
                    <p className="text-sm text-gray-500">ID: {user.id}</p>
                  </div>
                  <div className="flex items-center space-x-2">
                    <Badge className={getStatusBadge('Active')}>
                      Active
                    </Badge>
                    <Badge className={getRoleBadge(user.clinical_role_display)}>
                      {user.clinical_role_display}
                    </Badge>
                    <Button variant="outline" size="sm" onClick={() => handleEditUser(user)}>
                      <Edit className="h-4 w-4 mr-1" />
                      Edit
                    </Button>
                  </div>
                </div>
                
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-4">
                  <div className="flex items-start space-x-2">
                    <Mail className="h-4 w-4 text-gray-400 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Email</p>
                      <p className="text-sm text-gray-600">{user.email}</p>
                    </div>
                  </div>
                  
                  <div className="flex items-start space-x-2">
                    <Phone className="h-4 w-4 text-gray-400 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Phone</p>
                      <p className="text-sm text-gray-600">{user.phone_number || user.phone || 'No phone provided'}</p>
                    </div>
                  </div>
                  
                  <div className="flex items-start space-x-2">
                    <MapPin className="h-4 w-4 text-gray-400 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Site</p>
                      <p className="text-sm text-gray-600">{user.site_name || 'No site assigned'}</p>
                    </div>
                  </div>
                  
                  <div className="flex items-start space-x-2">
                    <Calendar className="h-4 w-4 text-gray-400 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Last Login</p>
                      <p className="text-sm text-gray-600">
                        {user.last_login ? new Date(user.last_login).toLocaleDateString() : 'Never logged in'}
                      </p>
                    </div>
                  </div>
                </div>
                
                <div className="pt-4 border-t border-gray-100">
                  <div className="flex items-center justify-between">
                    <div>
                      <p className="text-sm font-medium">Assigned Studies:</p>
                      <div className="flex flex-wrap gap-1 mt-1">
                        {user.assigned_studies > 0 ? (
                          <Badge variant="outline" className="text-xs">
                            {user.assigned_studies} studies assigned
                          </Badge>
                        ) : (
                          <span className="text-xs text-gray-500">No studies assigned</span>
                        )}
                      </div>
                    </div>
                    <div className="flex space-x-2">
                      <Button variant="outline" size="sm" onClick={() => handleAssignStudies(user)}>
                        Assign Studies
                      </Button>
                      <Button variant="outline" size="sm" onClick={() => handleViewProfile(user)}>
                        View Profile
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
            ))}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Development Notice */}
      <Card className="border-yellow-200 bg-yellow-50">
        <CardContent className="flex items-center space-x-3 pt-6">
          <AlertCircle className="h-5 w-5 text-yellow-600" />
          <div>
            <h3 className="font-medium text-yellow-800">Clinical Users Management</h3>
            <p className="text-sm text-yellow-700">
              This is a preview of the Clinical Users management system. Full functionality including 
              user creation, role assignment, study assignment, and profile management will be 
              implemented in the next phase.
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default ClinicalUsers;
